.App {
  width: 100%;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.TopButton {
  top: 85%;
  display: inline-block;
  left: 90%;
  position: fixed;
  width: 50px;
  height: 50px;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .TopButton {
    width: 20px;
    height: 20px;
    left: 90%;
  }
}
